import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';


class DocumentNumberModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Document Numbers
                        </Modal.Title>
                    </Modal.Header>

                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="DocumentPreFix">
                                <Form.Label>Document Pre Fix</Form.Label>
                                <Form.Control type="text" placeholder="Enter Document Pre Fix" 
                                value={this.props.DocumentPreFix}
                                onChange={this.props.changeEventandValidation}  name="DocumentPreFix" />
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorDocumentPreFix}
                                </div>
                            </Form.Group>

                            <Form.Group controlId="ModuleName">
                                    <Form.Label>ModuleName</Form.Label>
                                    <InputGroup>
                                        <Form.Control value={this.props.MODULE_ID}
                                            onChange={this.props.changeEventandValidation}
                                            name="ModuleID"
                                            hidden />
                                        <Form.Control
                                            type="text"
                                            value={this.props.MODULE_NAME}
                                            onChange={this.props.changeEventandValidation}
                                            placeholder="Search Module Name"
                                            aria-describedby="inputGroupPrepend"
                                            name="ModuleName"
                                            readOnly
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleModuleSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                    </InputGroup>
                                    <div className="invalid-feedback d-block">
                                        {this.props.ErrorModuleName}
                                    </div>
                                </Form.Group>

                            <Form.Group controlId="StartNumber">
                                <Form.Label>Start Number</Form.Label>
                                <Form.Control type="text" placeholder="Enter Start Number" 
                                value={this.props.StartNumber}
                                onChange={this.props.changeEventandValidation}  name="StartNumber" />
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorStartNumber}
                                </div>
                            </Form.Group>
                            <Form.Group controlId="LastUsedNumber">
                                <Form.Label>Last Used Number</Form.Label>
                                <Form.Control type="text" placeholder="Enter Last Used Number" 
                                value={this.props.LastUsedNumber}
                                onChange={this.props.changeEventandValidation}  name="LastUsedNumber" />
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorLastUsedNumber}
                                </div>
                            </Form.Group>


                            <Form.Group controlId="IncrementBy">
                                <Form.Label>Increment By</Form.Label>
                                <Form.Control type="text" placeholder="Enter Document Pre Fix" 
                                value={this.props.IncrementBy}
                                onChange={this.props.changeEventandValidation}  name="IncrementBy" />
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorIncrementBy}
                                </div>
                            </Form.Group>

                            <Form.Group controlId="SystemStatusType">
                                <Form.Label>System Status Type</Form.Label>
                                <InputGroup>
                                    <Form.Control value={this.props.SystemStatusTypeID}
                                        onChange={this.props.changeEventandValidation} hidden name="StatusID" />
                                    <Form.Control
                                        type="text"
                                        value={this.props.SystemStatusTypeDescription}
                                        // onChange={this.props.handleChangeSystemStatusTypeDescription}
                                        placeholder="Search System Status Type"
                                        aria-describedby="inputGroupPrepend"
                                        //required
                                        onChange={this.props.changeEventandValidation}
                                        readOnly
                                        name="Status"
                                    />
                                    <InputGroup.Prepend>
                                        <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                            <Search />
                                        </Button>
                                    </InputGroup.Prepend>
                                </InputGroup>
                                <div className="invalid-feedback d-block">
                                    {this.props.ErrorStatus}
                                </div>
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete DocumentNumber
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.DocumentPreFix} onChange={this.props.changeEventandValidation} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default DocumentNumberModal;